<template>
  <van-pull-refresh v-model="isLoading" @refresh="getMessage">
    <div :class="$style.message">
      <div :class="$style.notoken" v-if="!token">
        <span>未登录,点击<router-link to="/login">登陆</router-link></span>
      </div>
      <van-tabs v-model="active" v-else swipeable>
        <van-tab title="未读消息">
          <div v-if="noRead.length === 0" :class="$style.info">没有消息？下拉刷新试试！</div>
          <div v-else>
            <x-topic v-for="item in noRead" :key="item.id" :topicInfo="item"></x-topic>
          </div>
        </van-tab>
        <van-tab title="已读消息">
          <x-topic v-for="item in read" :key="item.id" :topicInfo="item"></x-topic>
        </van-tab>
      </van-tabs>
    </div>
  </van-pull-refresh>
</template>

<script>
import { PullRefresh, Tab, Tabs } from 'vant'
import { fetchMessage } from '@/util/api'
import { mapGetters } from 'vuex'
import Topic from './Topic'
export default {
  name: 'x-message',
  components: {
    [PullRefresh.name]: PullRefresh,
    [Tab.name]: Tab,
    [Topic.name]: Topic,
    [Tabs.name]: Tabs
  },
  data () {
    return {
      read: [],
      noRead: [],
      isLoading: false,
      active: 0
    }
  },
  computed: {
    ...mapGetters(['token'])
  },
  methods: {
    async getMessage () {
      if (this.token) {
        try {
          this.$toast.loading({mask: true, message: '加载中'})
          const res = await fetchMessage({accesstoken: this.token})
          if (res.success) {
            this.read = res.data.has_read_messages
            this.noRead = res.data.hasnot_read_messages
          }
          setTimeout(() => {
            this.$toast.clear()
            this.isLoading = false
          }, 500)
        } catch (error) {
          console.error(error)
          this.isLoading = false
        }
      } else {
        this.$toast.fail('未登录!')
        setTimeout(() => {
          this.isLoading = false
        }, 500)
      }
    }
  },
  activated () {
    this.getMessage()
  }
}
</script>

<style module>
.message{
  width: 100%;
  min-height: calc(100vh - 94px);
  background-color: #eeeeee;
}
.notoken{
  width: 100%;
  min-height: 400px;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.info{
  width: 100%;
  min-height: calc(100vh - 94px);
  font-size: 14px;
  color: #888;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
</style>



